export default {
	data() {
		return {
			getData: [],
			/**
			 * 结构
			 * {
				 img:'',//图片
				 describe:'',//描述
				 prices:'',//价格
				 code:''//识别码
				}
			 */
			showView: 0
		};
	},
	props: {
		dataList: '',
	},
	mounted() {
		console.log(Object.prototype.toString.call(this.dataList).replace("]", "").slice(8).trim());
		if (Object.prototype.toString.call(this.dataList).replace("]", "").slice(8).trim() == 'Array') {
			this.getData = this.dataList;
			this.showView = 1;
		} else {
			this.getData = this.dataList;
			this.showView = 2;
		}
	},
	methods: {
		outPut(code) {
			this.$emit('code', code);
		}
	}
}
